$selectedBackground: #bbbbbb;

* {
    font-family: 'Kavivanar', cursive;
}

h1 {
    a {
        color: black;           
    }
}

#version {
    margin-top: -45px;
    font-size: 30px;
}

.button, .group-selector {
    color: black;
    border: 1px solid black;
    padding: 4px 6px;
    text-decoration: none;
    cursor: pointer;
    background: rgba(211, 211, 211, 0.692);
}
.group-button {
    min-width: 22px
}
.group-selector {
    max-width: 260px;
}
.group-selector:nth-of-type(2) {
    border-top: none
}

.name-button:nth-of-type(2) {
    border-top: none;
}

#name-list-wrapper {
    display: flex;
    flex-wrap: wrap;
}
#name-list-button-wrapper {
    display: flex;
    flex-direction: column;
}
#name-list-title {
    display: inline
}
#name-list {
    list-style: none;
    padding: 3px;
    border: 1px solid black;
    overflow-y: scroll;

    min-width: 200px;
    min-height: 180px;
    max-height: 500px;

    // Names entered by the user
    .list-name { 
        margin: 3px 5px;
        padding: 3px;
        cursor: pointer;
    }
    .selected {
        background-color: $selectedBackground;
    }
}
#name-amount {
    border: 1px solid black;
    padding: 4px 6px;
}

//
// GROUPS
// Some button related css is still up above
//

.selected-group {
    background-color: #bcff7d;
}

#group-amount-form {
    margin-top: 5px;
}
// Removes up down arrows from chromium browsers
#group-amount::-webkit-outer-spin-button,
#group-amount::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
// Removes up down arrows from firefox
#group-amount[type=number] {
    -moz-appearance: textfield;
}

// Wrapper for .group divs
#groups {
    display: flex;
    flex-wrap: wrap;

    // Main div for each individual group
    .group {
        border: 1px solid black;
        max-width: 200px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        // Group title
        h4 {
            margin: 10px;
            font-size: 17px;
        }

        // Groups names wrapper
        .group-names {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;

            // Names in group
            p {
                font-size: 14px;
                margin: 7px;
            }
            .group-leader {
                background-color: $selectedBackground;
            }
        }
    }
}
#group-leader-form {
    min-width: 10rem;
    padding: 5px;
    

    #group-leader-checkbox {
        position: relative !important;
        display: inline-block !important;
        margin: 0;

    }
}